import { COLORS } from '@src/configs';
import * as React from 'react';

import {
  Col,
  ColPicked,
  HomeContainer,
  MovieTitle,
  PaymentActions,
  PaymentInfo,
  Row,
  TicketStatus,
  TicketWrapper,
} from './home.styled';
import { useAppContext } from '../../context/app.context';

type IHomeProps = {};
const Home = (_: IHomeProps) => {
  const [state, actions] = useAppContext();
  const [selected, setSelected]: [any, any] = React.useState({
    total: 0,
    tickets: {},
  });
  const [scale, setScale] = React.useState(false);
  const { tickets, meta, location } = state;
  const { types } = meta;
  const onTicketSelect = React.useCallback(
    (i, j) => {
      if (selected.tickets[`${i}_${j}`]) {
        setSelected({
          ...selected,
          tickets: {
            ...selected.tickets,
            [`${i}_${j}`]: null,
          },
          total: selected.total - 1,
        });
      } else {
        if (selected.total === 6) {
          return alert('max tickets pick');
        }
        setSelected({
          ...selected,
          tickets: {
            ...selected.tickets,
            [`${i}_${j}`]: tickets[i][j],
          },
          total: selected.total + 1,
        });
      }
    },
    [selected, state]
  );
  const calculatePrice = React.useMemo(
    () => {
      return Object.keys(selected.tickets).reduce((results, item) => {
        const ticket = selected.tickets[item];
        console.log(ticket, item, selected);
        if (ticket) {
          return results + meta[ticket.type].price;
        }
        return results;
      }, 0);
    },
    [selected]
  );
  React.useEffect(
    () => {
      setSelected({
        total: 0,
        tickets: {},
      });
    },
    [state.tickets]
  );
  return (
    <HomeContainer>
      <MovieTitle>
        <h2>Mắt biếc</h2>
      </MovieTitle>
      <TicketWrapper scale={scale}>
        {tickets.map((item: any, rowIndex: number) => {
          const seatRow: string = String.fromCharCode(65 + rowIndex);
          return (
            <Row key={'row' + rowIndex}>
              <span>{seatRow}</span>
              {item.map((_item: any, colIndex: number) => {
                if (_item.isPicked) {
                  return <ColPicked scale={scale} />;
                }
                return (
                  <Col
                    key={'col' + colIndex}
                    onClick={() => onTicketSelect(rowIndex, colIndex)}
                    scale={scale}
                    type={_item.type}
                    selected={!!selected.tickets[`${rowIndex}_${colIndex}`]}>
                    {!!selected.tickets[`${rowIndex}_${colIndex}`]
                      ? `${seatRow} ${colIndex + 1}`
                      : ''}
                  </Col>
                );
              })}
            </Row>
          );
        })}
      </TicketWrapper>
      <TicketStatus>
        <ul>
          {types.map((item: string) => {
            return (
              <li style={{ color: COLORS[item].default }}>
                {item}: {meta[item].price}
              </li>
            );
          })}
        </ul>
      </TicketStatus>
      <PaymentInfo>
        <div className="movie-time">
          <span>{location.name}</span>
          <span>
            {location.start} | {location.end}
          </span>
        </div>
        <div className="movie-total">{calculatePrice}</div>
      </PaymentInfo>
      <PaymentActions>
        <button onClick={() => setScale(!scale)}>Combo</button>
        <button
          onClick={() => {
            const result = actions.applyTickets(Object.keys(selected.tickets));
            console.log('results', result);
          }}>
          Thanh toán
        </button>
      </PaymentActions>
    </HomeContainer>
  );
};
export default Home;
